import { Component, OnInit } from '@angular/core';
import { AngularFireAuth } from 'angularfire2/auth';
import { AngularFireDatabase } from 'angularfire2/database';
import { AuthService } from '../../core/auth.service';
import { Router } from '@angular/router';
import { WebService } from '../shared/web.service';

@Component({
  selector: 'dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {

  public trainers_arr = [];
  public org_name = '';

  public dashboard_call;
  public org_trainers;
  public res_obj;

  public showPreloader = false;

  constructor(private _fbAuthSrv: AngularFireAuth,
              private _authSrv: AuthService,
              private _router: Router,
              private _webService: WebService,
              private db: AngularFireDatabase) { }

  ngOnInit() {
    // Set copy
    this.org_name = this._webService.org_name;

    // Check if user logged in, if not, send to login screen
    if(this._authSrv.authState == null) {
      this._router.navigate(['login']);
    } else {
      this.showPreloader = true;
      this._webService.dashboardCall().subscribe(res => {
        this.showPreloader = false;

        this.res_obj = JSON.parse(res['_body']);
        console.log(this.res_obj);

        this.org_trainers = this.res_obj['trainers'];
        this._webService.org_name = this.res_obj['trainer_org_external'];
        this._webService.org_name_internal = this.res_obj['trainer_org_internal'];
        this.org_name = this._webService.org_name;

        console.log(this.org_trainers);
  
        var trainers_arr = [];
        for(var key in this.org_trainers) {
          this.org_trainers[key]['show'] = false;
          trainers_arr.push(this.org_trainers[key]);
        }
  
        this.distribute_trainers(trainers_arr);
        console.log(trainers_arr);
        
        // this._webService.trainers_arr = this.org_trainers;
        this._webService.trainers_arr = this.org_trainers;
      })
    }
    
  }

  distribute_trainers(trainers:any[]) {
    // console.log(trainers.length);

    var outer_arr = [];
    var inner_arr = [];
    var counter_bool = true;

    for(var i = 0; i < trainers.length; i++) {
      if(counter_bool) {
        inner_arr.push(trainers[i]);
        counter_bool = !counter_bool;
      } else {
        inner_arr.push(trainers[i]);
        outer_arr.push(inner_arr);
        inner_arr = [];
        counter_bool = !counter_bool;
      }
      if(i == (trainers.length - 1)) {
        outer_arr.push(inner_arr);
      }
    }

    this.trainers_arr = outer_arr;
  }
}



