import { Component, OnInit } from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';
import { ActivatedRoute, Router, Params } from '@angular/router';
import { WebService } from '../shared/web.service';
import { ScheduleService } from '../shared/schedule.service';

@Component({
  selector: 'schedule',
  templateUrl: './schedule.component.html',
  styleUrls: ['./schedule.component.scss']
})
export class ScheduleComponent implements OnInit {

  public scheduleMultArr = [];

  public trainer_key;
  public trainer_schedule;

  private today = new Date();

  constructor(private _router: Router,
              private _activeRoute: ActivatedRoute,
              private _webService: WebService,
              private _SchedService: ScheduleService,
              private db: AngularFireDatabase) { }

  ngOnInit() {
    this._activeRoute.queryParams.subscribe((params: Params) => {
      this.trainer_key = params['trainer'];
      
      if(this._webService.trainers_arr == null || this.trainer_key == undefined) {
        this._router.navigate(['dashboard']);
      } else {
        this.trainer_schedule = this._webService.trainers_arr[this.trainer_key]['schedule'];
        // console.log(this.trainer_schedule);

        this.populateMultArr();        
      }
    })
  }

  populateMultArr() {

    var today = new Date();
    var dateInt = today.getDate();
    var monthInt = today.getMonth() + 1;
    var year = today.getFullYear();

    var date = (dateInt < 10) ? ('0' + dateInt) : dateInt;
    var month = (monthInt < 10) ? ('0' + monthInt) : monthInt;
    var date = (dateInt < 10) ? ('0' + dateInt) : dateInt;
    var month = (monthInt < 10) ? ('0' + monthInt) : monthInt;
    var todayDate = year + '-' + month + '-' + date;

    // Go through each entry in the trainer_schedule
    for(var key in this.trainer_schedule) {

      if(todayDate <= key) {
        // console.log(key);
        this._SchedService.getHumanDate(key);

        var dateObj = {
          date: key,
          appointments: [],
          date_human: this._SchedService.getHumanDate(key)
        }

        for (var key1 in this.trainer_schedule[key]) {
          dateObj.appointments.push(this.trainer_schedule[key][key1]);
        }

        this.scheduleMultArr.push(dateObj);
      }

      // console.log(this.scheduleMultArr);
    }
  }

  deleteAppointment(day) {
    console.log(day);
    alert("This features coming soon! - The Notifly Team");
  }

}



