import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from '../../core/auth.service';

import { WebService } from '../../ui/shared/web.service';
import { ReactiveFormsModule } from '@angular/forms';


@Component({
  selector: 'user-login',
  templateUrl: './user-login.component.html',
  styleUrls: ['./user-login.component.scss']
})
export class UserLoginComponent implements OnInit {

  constructor(public auth: AuthService,
              private router: Router,
              private _webSrv: WebService) { }


  ngOnInit() {
    this.userTracking();
    this.auth.authChange.subscribe(res => {
      if(res) {
        console.log("Logged In");
        this.router.navigate(['/']);
      } else {
        console.log("Not Logged In");
      }
    })
  }


  // User tracking
  userTracking() {
    console.log('check ls');
    if(localStorage.getItem('notifly-login-page-user') == undefined) {
      console.log("landing here for the first time");
      var anon_uid = 'user-' + Date.now();
      localStorage.setItem('notifly-login-page-user', anon_uid);
      this._webSrv.anonActivityTracker(anon_uid, 'first-landing').subscribe(res => res);
    } else {
      console.log("revisiting login page as anon-user");
      var anon_uid = localStorage.getItem('notifly-login-page-user');
      this._webSrv.anonActivityTracker(anon_uid, 'revisiting_login_page').subscribe(res => res);
    }
  }

  /// Social Login

  signInWithGithub(): void {
    this.auth.githubLogin()
    .then(() => this.afterSignIn());
  }

  signInWithGoogle(): void {
    this.auth.googleLogin()
      .then(() => this.afterSignIn());
  }

  signInWithFacebook(): void {
    this.auth.facebookLogin()
      .then(() => this.afterSignIn());
  }

  signInWithTwitter(): void {
    this.auth.twitterLogin()
      .then(() => this.afterSignIn());
  }

  /// Anonymous Sign In

  signInAnonymously() {
    this.auth.anonymousLogin()
      .then(() => this.afterSignIn());
  }


  /// Shared

  private afterSignIn(): void {
    // Do after login stuff here, such router redirects, toast messages, etc.
    this.router.navigate(['/']);
  }

}



