import { Component, OnInit } from '@angular/core';
import { AngularFireAuth } from 'angularfire2/auth';
import { AngularFireDatabase } from 'angularfire2/database';
import { AuthService } from '../../core/auth.service';
import { Router } from '@angular/router';
import { WebService } from '../shared/web.service';

@Component({
  selector: 'app-add-trainer',
  templateUrl: './add-trainer.component.html',
  styleUrls: ['./add-trainer.component.css']
})
export class AddTrainerComponent implements OnInit {

  public org_name;

  public fname = '';
  public lname = '';
  public email = '';
  public phone = '';

  public showPreloader = false;

  constructor(private _fbAuthSrv: AngularFireAuth,
              private _authSrv: AuthService,
              private _router: Router,
              private _webService: WebService,
              private db: AngularFireDatabase) { }

  ngOnInit() {
    if(this._webService.org_name_internal == '')  {
      this._router.navigate(['dashboard']);
    } else {
      this.org_name = this._webService.org_name_internal;
    }
  }

  validateEmail(email) {
    var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    
    if(!re.test(email)){
      alert('Enter a Valid Email Address');
      return false;
    }
    return true;
  }

  validatePhone(phone:string) {
     var re = /^[\+]?[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{4,6}$/im;
     if (!re.test(phone) && phone.length != 10){
        alert('Enter a Valid, 10-digit Phone Number');
        return false;
     }
     return true;
  }

  addTrainer() {
      //firebase request

      const trainer = {
        firstName: this.fname,
        lastName: this.lname,
        email: this.email,
        phone: this.phone,
        org: this.org_name
      }

      if (this.validateEmail(this.email) && this.validatePhone(this.phone) && this.fname != '' && this.lname != ''){
        this.showPreloader = true;
        console.log(trainer)
        this._webService.newTrainer_v2(trainer).subscribe(Response => {
          this.showPreloader = false;
          console.log(Response);
          alert("Great, we've added " + this.fname + " as a trainer.");
          
          this.fname = '';
          this.lname = '';
          this.phone = '';
          this.email = '';
        })
      } else {
        console.log('Invalid phone number or email.')
      }
  }


}



